<template>
    <v-container id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/ujicpkb" link>Back</v-btn>
        <v-row id="print" no-gutters>
            <v-row no-gutters>
                <v-col cols="2">
                    <h3 class="mt-2">No LP</h3>
                </v-col>
                <v-col cols="2">
                    <v-autocomplete single-line outlined dense="" :items="dbelicpkbs" class="title" item-text="No LP"
                    item-value="No LP" label="Nomor LP" v-model="noLP" @change="getDataCPKB()">
                    <template slot="item" slot-scope="data">
                        {{ data.item["No LP"] }} - {{ data.item["Kode Barang CPKB"] }}
                    </template>
                </v-autocomplete>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-right mr-4">Nama Barang</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title"  dense single-line outlined  label="Nama Barang" v-model="dbelicpkbforms['Nama Produk']"></v-text-field>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-right mr-4">Kode Barang</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title"   dense single-line outlined label="Kode Barang" v-model="dbelicpkbforms['Kode Produk']"></v-text-field>
                </v-col>
            </v-row>
            <v-row no-gutters>
                <v-col cols="2">
                    <h3 class="mt-2">Pemasok</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title"  dense single-line outlined label="Pemasok" v-model="dbelicpkbforms['Nama Suplier']"></v-text-field>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-center">No Bets</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title"  dense single-line outlined label="No Bets" v-model="dbelicpkbforms['No Bets']"></v-text-field>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-center ml-3">Jumlah</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title" type="number" dense single-line outlined label="xxx ltr/kg/etc" v-model="jumlahWadah"></v-text-field>
                </v-col>
            </v-row>
            <v-row no-gutters>
                <v-col cols="2">
                    <h3 class="mt-2">No COA</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title"  dense single-line outlined label="No COA" v-model="noCOA"></v-text-field>
                </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-center ml-4">Tgl Ditolak</h3>
                </v-col>
                <v-col cols="2" style="display: flex">
                                <v-menu ref="menu" v-model="menu" :close-on-content-click="false"
                                    :return-value.sync="date" transition="scale-transition">
                                    <template v-slot:activator="{ on }">
                                        <v-text-field dense single-line outlined v-model="tanggal"
                                            label="Tanggal Ditolak" class="title" readonly v-on="on"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="tanggal" no-title scrollable>
                                        <v-spacer></v-spacer>
                                        <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                        <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>
                <v-col cols="2">
                    <h3 class="mt-2 text-right mr-5">Ditolak Oleh</h3>
                </v-col>
                <v-col cols="2">
                    <v-text-field class="title" dense single-line outlined label="Ditolak Oleh" v-model="oleh"></v-text-field>
                </v-col>
            </v-row>
        </v-row>
        <template id="template">
                <table >
                    <tr v-for="i in parseInt(jumlahWadah)" :key='i.id'>
                        <td style="padding-bottom: 0.648229167cm;">
                            <table id="tabel" height="264px">
                                <tr>
                                    <th id="td" width="120px" ><img style="vertical-align: middle;" src="../../assets/logoNiagara.png" width="75px"
                                            height="20"></th>
                                    <th id="td" width="180px" style="text-align:center;">DITOLAK</th>
                                    <th id="td" width="48"><img style="vertical-align: middle;" src="../../assets/red.png" width="20px" height="20"></th>
                                </tr>
                                <tr>
                                    <td id="td">Nama Barang</td>
                                    <td id="td" colspan="2" style="text-align: center;">{{ dbelicpkbforms["Nama Produk"] }}</td>
                                </tr>
                                <tr>
                                    <td id="td">Kode Barang</td>
                                    <td id="td" colspan="2" style="text-align: center;">{{ dbelicpkbforms["Kode Produk"] }}</td>
                                </tr>
                                <tr>
                                    <td id="td">No Bets</td>
                                    <td id="td" colspan="2" style="text-align: center;">{{ dbelicpkbforms["No Bets"] }}</td>
                                </tr>
                                <tr>
                                    <td id="td">Pemasok</td>
                                    <td id="td" colspan="2" style="text-align: center;">{{ dbelicpkbforms['Nama Suplier'] }}</td>
                                </tr>
                                <tr>
                                    <td id="td">No LP</td>
                                    <td id="td" colspan="2" style="text-align: center;">{{ noLP }}</td>
                                </tr>
                                <tr>
                                    <td id="td">Jumlah</td>
                                    <td id="td" colspan="2" style="text-align: center;">{{ jumlahWadah }}</td>
                                </tr>
                                <tr>
                                    <td id="td">No COA</td>
                                    <td id="td" colspan="2" style="text-align: center;">{{ noCOA }}</td>
                                </tr>
                                <tr>
                                    <td id="td">Ditolak Tgl:<br><br> Oleh</td>
                                    <td id="td" colspan="2" style="vertical-align: top; text-align: center;">{{ tanggal.substring(0,10) }}<br><br><b>{{ oleh }}</b></td>
                                </tr>
                            </table>
                        </td>
                    </tr>
                </table>
        </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                date: new Date().toISOString().substr(0, 10),
                menu: false,
                noLP:'',
                jumlahWadah: 0,
                noCOA: '',
                tanggal: '',
                oleh: '',
                dbelicpkbs: [],
                dbelicpkbforms: [],
                spesifikasiforms: [],
                NoRules: [
                    value => (value || '').length == 4 || 'INPUTAN HARUS 4 Karakter',
                ],
                nomor:'',
                nomorUji:'',
                keyword: '',
                jumlahData: 1,
                load: false,
                laporanujibs: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'dbelicpkbs/dataujitidaklulus'
                this.$http.get(uri).then(response => {
                    this.dbelicpkbs = response.data.data
                })
            },

             getDataCPKB() {
                 if(this.noLP){
                    var uri = this.$apiUrl + 'dbelicpkbs/datauji/' + this.noLP
                    this.$http.get(uri).then(response => {
                        this.dbelicpkbforms = response.data.data[0]
                        console.log(this.dbelicpkbforms)
                    })
                 }
             },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
      
    }
    #tabel{
        border-collapse: collapse;
    }
    #tabel #th, #td {
        border: 1px solid black;
    }
</style>